@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.infoBox {
  flex: 1 1;
  padding: 0 12px;
  cursor: pointer;
}

@media (min-width: 600px) {
  .infoBox:not(:last-child) {
    margin-right: 15px;
  }
}

@media (max-width: 600px) {
  .infoBox {
    margin-top: 16px;
  }
}

.infoBox--active {
  box-shadow: 0px 0px 10px 3px rgba(0, 0, 0, 0.33) !important;
}

.infoBox__title {
  font-weight: bold;
}

.infoBox__label {
  display: flex;
  justify-content: space-between;
  padding-top: 12px;
}

.infoBox__span {
  font-weight: bold;
}

.infoBox--isRed {
  color: #cc1034;
}

.map {
  height: 500px;
  background-color: white;
  padding: 15px;
  border-radius: 4px;
  margin-top: 16px;
  box-shadow: 0 0 8px -4px rgba(0, 0, 0, 0.5);
}

.map .leaflet-container {
  height: 100%;
  border-radius: 4px;
}

.info {
  margin-bottom: 5px;
}

.table-container {
  overflow: scroll;
  height: 400px;
}

.table {
  overflow: scroll;
  width: 100%;
  color: #6a5d5d;
  text-align: left;
}

#title-row {
  background-color: white;
}

.title {
  -webkit-margin-before: 0;
          margin-block-start: 0;
  padding-bottom: 10px;
}

.table tr {
  display: flex;
  justify-content: space-between;
  padding: 3px 15px;
}

.table tr:nth-of-type(odd) {
  background-color: #f3f2f8;
}

.chart-title {
  padding: 15px;
  text-transform: capitalize;
}

.chart-container {
  height: 250px;
}

.chart--noData {
  padding: 10px 15px;
}

@media (max-width: 990px) {
  .chart-container {
    width: 100%;
  }
}

* {
  margin: 0;
  font-family: Montserrat !important;
  color: #6a5d5d;
}

body {
  background-color: #f5f6fa;
}

.app__dropdown {
  background-color: white;
}

.app {
  margin: auto;
  margin-top: 60px;
  display: flex;
  justify-content: space-evenly;
  padding: 20px;
  height: auto;
  max-width: 1280px;
}

.app__left {
  width: 60%;
  padding-right: 24px;
}

.app__right {
  width: 40%;
}

.app__header {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  justify-content: space-between;
}

@media (max-width: 600px) {
  .app__header {
    flex-direction: column;
    margin-bottom: 0px;
  }

  .app__title {
    margin-bottom: 10px;
    white-space: normal;
    white-space: initial;
  }

  .app__dropdown {
    width: 100%;
  }

  .app__left {
    display: flex;
    flex-direction: column;
  }

  .app__stats {
    order: 2;
  }
}

.app__title {
  font-size: 32px;
  letter-spacing: 2px;
  text-transform: uppercase;
}

.app__stats {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

@media (max-width: 990px) {
  .app {
    flex-direction: column;
  }

  .app__left {
    width: 100%;
    padding-right: 0px;
  }

  .app__right {
    margin-top: 16px;
    width: 100%;
  }
}

@media (max-width: 600px) {
  .app__stats {
    display: flex;
    flex-direction: column;
  }
}

